
:root{
    --bg-dark: #323133;
    --logo-height: 100px;
    --header-height: 100px;
    --aside-width: 225px;
    --footer-height: 40px;
    --menu-top-height: 100px;

    --shadow: 0 2px 23px 0 rgba(0, 0, 0, 0.1),
              0 2px 49px 0 rgba(0, 0, 0, 0.06);
}

*{
    box-sizing: border-box;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif
}

.app{
    margin: 0px;
    display: grid;
    grid-template-columns: var(--aside-width) 1fr;
    grid-template-rows: var(--header-height) 1fr var(--footer-height);
    grid-template-areas: 
        "menu header "
        "menu content"
        "menu footer";
    height: 100vh;
    background-color: #f5f5f5;
    }

aside.logo{
    grid-area: logo;
}

header.header{
    grid-area: header;
}

aside.menu-area{
    grid-area: menu;
    
    width: var(--aside-width); 
}

main.content{
    grid-area: content;
}

footer.footer{
    grid-area: footer;
}
.data-warning{
    text-align: justify;
}

.modal {
        border: 1px solid red;
        width: 100%;
        height: 100vh;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 10;
        background-color: rgba(0, 0, 0, 0.5);
        display: flex;
        justify-content: center;
        align-items: center;
}

.container{
    background-color: #fff;
    color: #000;
    width: 60%;
    height: 60%;
    border-radius: 15px;
}

@media(max-width: 768px){
    .app{
        grid-template-rows: var(--header-height)
                            var(--menu-top-height)
                            1fr
                            var(--footer-height);
        grid-template-columns: var(--aside-width) 1fr;
        grid-template-areas: 
            "logo header"
            "menu menu"
            "content content"
            "footer footer";
    }
}

@media(max-width: 576px){
    .app{
        grid-template-rows: 
            var(--logo-height)
            var(--menu-top-height)
            1fr
            var(--footer-height);
    }
}

