header.header{
    background-color: #fff;
    color: rgb(22, 21, 21);
    padding: 0 15px;
    overflow: hidden;
    white-space: nowrap;
    box-shadow: var(--shadow);
    
}

header.header h1{
    font-size: 1.8em;
}

.button{
    width: 100%;
    font-size: 2em;
    border-radius: 10px;
    border: none;
    text-decoration: none;
    background: #ccc;
    outline: none;
}

.headerContainer{
    display: flex;
    justify-content: center;
    align-items: center;
}

.buttonContainer{
    display: flex;
    justify-content: center;

}

.header-title{
    width: 95%;
    
}