aside.menu-area{
    background-color: var(--bg-dark);
}

.menu a{
    display: block;
    text-decoration: none;
    color: #fff;
    font-size: 1.5em;
    padding: 5px 14px;
}

.menu a:hover{
    background: linear-gradient(to right, #07a7e3, #32dac3 100%);
}

.fa-home{
    font-size: 1.3em;
}

@media(max-width: 768px){
    .menu{
        display: flex;
        justify-content: flex-start;
        align-items: center;
    }
    .menu a{
        display: inline;
        padding: 10px;
        margin: 0px;
    }

    .menu{
        height: 100%;
        justify-content: space-around;
    }
}